.about-container{
    width: 100%;
    display: flex;
    background-color: white;
    color: black;
    height: 70vh;
    padding: 100px 0 100px 0;
}

.about-desc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 55%;
    padding: 0 100px 0 100px ;
}

.about-desc h3{
    margin-bottom: 30px;
    font-size: 28px;
}

.about-desc p{
    line-height: 1.5;
    font-size: 18;
}

.about-img{
    width: 45%;
    height: 100%;
    display: flex;
    align-self: center;
    overflow: hidden;
    padding: 10px 0 10px 0 ;
}

@media only screen  and  (max-width: 960px){
    .about-container{
        flex-direction: column;
        padding: 0;
        height: auto;
    }

    .about-desc{
        width: 90%;
        margin: 0 auto;
        padding: 40px 20px 50px 20px;
    }

    .about-desc h3 {
        font-size: 20px;
        text-align: center;
    }

    .about-desc p {
        font-size: 15px;
        text-align: center;
    }

    .about-img {
        width: 100%;
        margin-bottom: 50px;
    }

    .about-img img {
        width: 100%;
    }
    
}