.footer{
    width: 100%;
    height: 100vh;
    background-color: rgb(6, 59, 68);
    color: white;
    padding-top: 180px;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left: 0;
}

.footer-info{
    font-size: 35px;
    padding: 30px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footer-info h1{
    padding-bottom: 15px;
}

.footer-contact{
    font-size: 22px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

.footer-sns{
    width: 70%;
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

.sns-links {
    grid-template-columns: 1fr 1fr 1fr;
    display: grid;
    width: 100%;
}

.twitter, .github, .linkedin{
    color: white;
    font-size: 35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}

.twitter:hover, .github:hover, .linkedin:hover {
    border-color: white;
    transition: all 0.3s ease-out;
}

.design-by{
    font-size: 12px;
    padding-left: 120px;
    color: white;
}


@media only screen and (max-width: 960px){
 
  .footer-info{
      text-align: center;
      padding: 0;
      font-size: 15px;
  }
  
  .footer-contact{
      font-size: 16px;
      padding: 100px 0 30px 0;
      text-align: center;
  }

  .footer-sns{
      grid-template-columns: 1fr;
      text-align: center;
  }

  .design-by{
      padding: 40px 0 0 0;
      font-size: 12px;
  }
}